import React, {useState, useEffect, useReducer, Component } from 'react'
import Taro from '@tarojs/taro'
import { CoverView, CoverImage } from '@tarojs/components'
import { observer, inject } from 'mobx-react'
import './index.styl'
import Icon1 from '../static/tabImg/sign1.png'
import Icon2 from '../static/tabImg/sign2.png'
import Icon3 from '../static/tabImg/sign3.png'
import Icon4 from '../static/tabImg/sign4.png'
import counterStore from '../store/counter';

const CustomTabBar=observer((prop)=>{
    console.log("自定义 tabbar 触发 渲染",prop);
    let [selected,setselected]=useState("");
    let [color,setColor]=useState("#999");
    let [selectedColor,setselectedColor]=useState("#B90220")
    let [list,setlist]=useState([
        {
            pagePath: "/pages/index/index",iconPath: Icon1,selectedIconPath: Icon2,text: "首页"
        },
        {
            pagePath: "/pages/home/home",iconPath: Icon3,selectedIconPath: Icon4,text: "我的"
        }
    ])
    let [num,setnum]=useState(9);
    useEffect(()=>{
        setselected(counterStore.selected);
    })
    return (
        <CoverView className='custom-tab'>
            {
                list.map((item, index) => {
                    return <CoverView className='custom-tab-item' onClick={()=>{switchTab(index)}} data-path={item.pagePath} key={index}>
                        <CoverImage className='custom-tab-item-img' src={selected === index ? item.selectedIconPath : item.iconPath} />
                        {index === 1 ? <CoverView className='custom-tab-item-num'>{num}</CoverView> : ''}
                        <CoverView className='custom-tab-item-text' style={{ color: selected === index ? selectedColor : color }}>
                            {item.text}
                        </CoverView>
                    </CoverView>
                })
            }
        </CoverView>
    )
    function switchTab(index){
        const url = list[index].pagePath
        Taro.switchTab({
            url: url
        })
        //setselected(index);
        counterStore.changeSelected(index);
    }

})
export default CustomTabBar




